.component--hero {
  position: relative;
  /* top: 58px; */
  width: 100%;
  height: 100%;
  background: rgb(247, 148, 29);
  background: linear-gradient(90deg, rgba(247, 148, 29, 1) 0%, rgba(254, 174, 79, 1) 74%, rgba(251, 197, 112, 1) 100%);
}

.component--hero .hero-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: auto;
  min-height: 225px;
  max-width: 768px;
  width: 100%;
  padding: 25px 2.5%;
  margin: auto;
}

.component--hero .hero-container h1 {
  margin: 0;
}

.component--hero .hero-container p {
  margin: 0;
}

.component--hero .hero-container .cta-container {
  margin: 25px 0;
}
