.btn {
  font-weight: bold;
  text-decoration: none;
  padding: 7px 14px;
  border: 2px solid;
  border-radius: 3px;
  transition: all 0.25s ease-out;
}

.btn:first-child {
  margin: 0 3px 0 0;
}

.btn:last-child {
  margin: 0 0 0 3px;
}

.btn:hover {
  box-shadow: 0 3px 12px -3px rgba(0, 0, 0, 0.675);
}

.btn:focus {
  opacity: 0.6;
}

.btn-primary {
  background-color: black;
  color: white;
  border-color: #f7941d;
}

.btn-secondary {
  background-color: white;
  color: black;
  border-color: black;
}
